.main {
    display:block;
    margin: 0;
    overflow-x: hidden;
    padding:0;

    &__header { 
        //background: url('../../assets/Header-Background5.jpg') no-repeat center center; 
        background: url('../../assets/background-test.jpg') no-repeat center center; 
        -webkit-background-size: cover;
        -moz-background-size: cover;
        -o-background-size: cover;
        background-size: cover;
        height:100%;
        display:block;

        &__name {
            color:$color-white;
            font-size:$font-header;
            text-align: center;
            padding-top:5vw;
            margin: auto auto -5vw auto;
            font-family: 'Langar', cursive;
            z-index: 999999999999 !important;
        }

        &__horizontal {
            width: 20vw;
                margin:auto;
            display:flex;
            height:7px;
            border-radius: 10px;
            background-color: $color-white;
        }

        &__aboutOuter {
            margin-left:20vw;
        }

        &__about {
            color:$color-white;
            font-size:10rem;
            text-align:center;
            margin-top:3vw;
            line-height: normal;
            font-family: 'Langar', cursive;
            display:inline-block;
        }

        &__logos {
            align-items: center;
            display:flex;
            flex-wrap: wrap;
            align-items: center;
            justify-content: center;
            margin:auto;

            &__link {
                width:3vw; 
                height:3vw;
                align-self:center;
                margin:auto 1vw auto 1vw;
                display: inline-flex;
                z-index: 999999999999 !important;
            }

            &__logo {
                width:5vw; 
                height:5vw;
                align-self:center;
                margin:auto 1vw auto 1vw;
                display: inline-flex;
                z-index: 999999999999 !important;
                color: $color-primary;
            }

            &__logo:hover {
                transform: scale(1.2);
                transition: all 0.5s;
            }
        }
        
        &__arrow {
            position: relative;
            bottom: -2rem;
            left: 50%;
            margin-left:-20px;
            width: 40px;
            height: 40px;
            margin-top: 5vw;
            z-index: 999999;

            /**
             * Dark Arrow Down
             */
            background-image: url(data:image/svg+xml;base64,);
            background-size: contain;
        }

        @include respond(big-desktop) {
        }
    
        @include respond(tab-land) {
            &__name {
                font-size: 15rem;
            }

            &__about {
                font-size: 8rem;
            }

            &__logos {
    
    
                &__logo {
                    width:5vw; 
                    height:5vw;
                }
            }
        }
        @include respond(tab-port) {
            
        }
        @include respond(phone) {
        }
        @include respond(small-phone) {
        }
    }
}

.animation {
    // margin-top: -50vw;
    position: absolute;
    z-index: 99999;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}